import Vue from 'vue'
import Router from 'vue-router'
import Home from '../pages/Home'
import Formacao from '../pages/Formacao'
import Consulta from '../pages/Consulta'
import Contato from '../pages/Contact'
import Faq from '../pages/Faq'

Vue.use(Router)

export default new Router({
  mode: 'history',
  base: '/',
  routes: [
    {
      path: '/',
      name: 'Home',
      component: Home
    },
    {
      path: '/formacao',
      name: 'Formacao',
      component: Formacao
    },
    {
      path: '/agendamento',
      name: 'Agendamento',
      component: Contato
    },
    {
      path: '/consulta',
      name: 'Consulta',
      component: Consulta
    },
    {
      path: '/faq',
      name: 'FAQ',
      component: Faq
    }
  ]
})
